import { Component, OnInit, ViewChild, AfterViewInit } from '@angular/core';
import { ContactModalComponent } from '../contact-modal/contact-modal.component';
import Swiper, { Navigation, Pagination } from 'swiper';

@Component({
  selector: 'app-landing',
  templateUrl: './landing.component.html',
  styleUrls: ['./landing.component.sass']
})
export class LandingComponent implements OnInit, AfterViewInit {

  insuranceLogos = [
    {path: '../../assets/insurance_logos/Allianz_logo.svg'},
    {path: '../../assets/insurance_logos/AXA_Logo.svg'},
    {path: '../../assets/insurance_logos/ARAG_Logo.svg'},
    {path: '../../assets/insurance_logos/Ergo_Versicherungsgruppe_logo.svg'},
    {path: '../../assets/insurance_logos/Gothaer.svg'},
    {path: '../../assets/insurance_logos/HDI-Logo.svg'},
    {path: '../../assets/insurance_logos/R+V-Logo.svg'},
    {path: '../../assets/insurance_logos/SV_SparkassenVersicherung_logo.svg'}
  ];

  swiper: any = undefined;

  constructor() { }

  @ViewChild('contactModal') contactModal: ContactModalComponent;
  // @ViewChild('logoCarousel', {static: false}) logoCarousel;


  ngOnInit(): void {
  }

  ngAfterViewInit(): void {
    this.initSwiper();
  }

  initSwiper(): void {

    Swiper.use([Navigation, Pagination]);
    this.swiper = new Swiper('.swiper-container', {
      on: {
        init: () => {
          console.log('swiper initialized');
        },
        touchEnd: (swiperInstance, $event) => {
          // this.swiper.autoplay.start();
        }
      },
      // navigation: {
      //   nextEl: '.swiper-button-next',
      //   prevEl: '.swiper-button-prev',
      // },
      slidesPerView: 5,
      spaceBetween: 50,
      pagination: {
        el: '.swiper-pagination',
        type: 'bullets'
      },
      autoHeight: false,
      loop: true,
      autoplay: {
        delay: 2000
      },
      breakpoints: {
        // when window width is >= 320px
        320: {
          slidesPerView: 2,
          spaceBetween: 20
        },
        // when window width is >= 480px
        480: {
          slidesPerView: 3,
          spaceBetween: 30
        },
        // when window width is >= 640px
        640: {
          slidesPerView: 5,
          spaceBetween: 50
        }
      }
    });
    Swiper.use([Navigation, Pagination]);
    console.log('swiper: ', this.swiper);

  }

  toggleModal(): void {
    this.contactModal.toggleModal();
  }

}
